import InputArea from "../../plugin/components/InputArea";
export default {
  name: "userList",
  components: {
    InputArea
  },
  data() {
    return {
      currentPage: 1,
      pagesize: 5,
      total: 0,
      dialogAddRole: false,
      dialogTitle: "",
      formLabelWidth: "120px",
      roleId: -1, //权限分配id
      form: {
        role_id: -1,
        name: "",
        display_name: ""
      },
      form2: {
        role: ""
      },
      dialogRoleAssign: false,
      dialogRightAssign: false,
      tableData: [],
      multipleSelection: [],
      rightData: [],
      defaultProps: {
        children: "children",
        label: "display_name"
      },
      checkedKeys: [], //默认选中的权限id
      currentRole: {} //记录点击权限按钮时的角色信息
    };
  },

  methods: {
    async getRoleList() {
      const res = await this.$http.get("v1/backend/auth/roles", {
        params: {
          page: this.currentPage,
          limit: this.pagesize
        }
      });
      console.log(res);
      if (res.data.code === 200) {
        this.tableData = res.data.data;
        this.total = res.data.total;
        this.$message({
          type: "success",
          message: res.data.msg
        });
      } else {
        this.$message({
          type: "info",
          message: res.data.msg
        });
      }
    },
    async getRigthList() {
      const res = await this.$http.get("v1/backend/auth/permissions");
      console.log(res);
    },
    async addRoleFn() {
      if (!this.form.role_id) {
        const res = await this.$http.post("v1/backend/auth/roles", {
          name: this.form.name,
          display_name: this.form.display_name
        });
        if (res.data.code === 200) {
          this.dialogAddRole = false;
          this.getRoleList();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        } else {
          this.$message({
            type: "info",
            message: res.data.msg
          });
        }
      } else {
        console.log(this.form);
        const res = await this.$http.put("v1/backend/auth/roles", {
          name: this.form.name,
          display_name: this.form.display_name,
          role_id: this.form.role_id
        });
        console.log(res);
        if (res.data.code === 200) {
          this.dialogAddRole = false;
          this.getRoleList();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        } else {
          this.$message({
            type: "info",
            message: res.data.msg
          });
        }
      }
    },
    async handleRights() {
      // 获取所有选中的权限id
      const nodes = this.$refs.tree.getCheckedNodes();
      console.log(nodes);
      let arr = [];
      nodes.forEach(item => {
        // 选中的子权限id
        arr.push(item.id.toString());

        // 子权限的id 对应的父权限的id
        // if (typeof item.parent_id === "number") {
        //   arr.push(item.parent_id.toString());
        // } else {
        //   arr = arr.concat(item.parent_id.split(","));
        // }
        console.log(arr);
      });

      // 数组去重
      const set = new Set(arr);

      const ids = [...set].join(",");
      console.log(ids);
      let data = {
        permission_ids: ids,
        role_id: this.currentRole.id
      };
      const res = await this.$http.post(
        "v1/backend/auth/permission-assignment",
        data
      );
      console.log(res);
      if (res.data.code === 200) {
        this.dialogRightAssign = false;
        this.$message({
          type: "success",
          message: res.data.msg
        });
      } else {
        this.$message({
          type: "error",
          message: res.data.msg
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(this.multipleSelection);
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pagesize = val;
      this.getRoleList();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getRoleList();
    },
    addRole() {
      this.dialogAddRole = true;
      this.dialogTitle = "添加角色";
      this.form = {
        name: "",
        diaplay_name: ""
      };
    },
    handleEdit(index, row) {
      console.log(index, row);
      this.dialogAddRole = true;
      this.dialogTitle = "编辑角色";
      this.form.name = row.name;
      this.form.display_name = row.display_name;
      this.form.role_id = row.id;
    },
    // async editRoleFn() {
    //   console.log('woshi')
    //   const res = await this.$http.put('auth/roles',{
    //     username: this.form.name,
    //     display_name: this.form.display_name
    //   })
    //   console.log(res)
    // },
    handleRole(index, row) {
      this.dialogRoleAssign = true;
    },
    async showRightsDialog(row) {
      console.log(row);
      this.checkedKeys = [];
      this.dialogRightAssign = true;
      this.currentRole = row;
      this.roleId = row.id;
      // this.$nextTick(() => {
      //   this.$http.get("v1/backend/auth/permissions").then(res => {
      //     console.log(res);
      //     if (res.data.code === 200) {
      //       this.rightData = res.data.data;
      //     }
      //   });
      // });
      const res = await this.$http.get("v1/backend/auth/permissions");
      console.log(res);
      if (res.data.code === 200) {
        this.rightData = res.data.data;
      }
      //获取当前角色具有的权限
      function getCurrentRoleRights(rightsList) {
        console.log(rightsList);
        const arr = [];
        const fn = function(list) {
          console.log(list, list.length);
          if (list.length === 0 || undefined) {
            return undefined;
          }
          list.forEach(item => {
            // arr.push(item.id)
            if (!item.children) {
              arr.push(item.id);
            } else {
              arr.push(item.id);
              fn(item.children);
            }
          });
        };
        fn(rightsList);
        return arr;
      }
      const result = await this.$http
        .get("v1/backend/auth/permission-assignment", {
          params: {
            role_id: row.id
          }
        })
        .then(res => {
          console.log(res);
          console.log(row.id);
          if (res.data.code === 200) {
            this.checkedKeys = getCurrentRoleRights(res.data.data);
            // this.getRoleList()
          }
          console.log(this.checkedKeys);
        });
    },
    handleDelete(index, row) {
      this.form.role_id = row.id;
      console.log(this.form.role_id);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          // let data = {
          //   role_id: this.form.role_id
          // }
          const res = await this.$http.delete("v1/backend/auth/roles", {
            params: {
              role_id: this.form.role_id
            }
          });
          if (res.data.code === 200) {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.getRoleList();
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  },
  mounted() {
    this.getRoleList();
    this.getRigthList();
  }
};



